import React from 'react'
import Link from 'gatsby-link'
import get from 'lodash/get'
import Helmet from 'react-helmet'
import Waypoint from 'react-waypoint'
import * as PropTypes from 'prop-types'

import Header from '../components/Header'
import Nav from '../components/Nav'
import pic01 from '../assets/images/pic01.jpg'

const propTypes = {
  data: PropTypes.object.isRequired,
}

class AssignmentTemplate extends React.Component {
  _handleWaypointEnter = () => {
    this.setState(() => ({ stickyNav: false }))
  }

  _handleWaypointLeave = () => {
    this.setState(() => ({ stickyNav: true }))
  }
  render() {
    const assignment = this.props.data.assignments
    /* console.log('assignment' + assignment) */
    return (
      <div>
        <Helmet title={get(this, 'props.data.site.siteMetadata.title')} />
        <Header />
        <Waypoint
          onEnter={this._handleWaypointEnter}
          onLeave={this._handleWaypointLeave}
        />
        {/* <Nav sticky={this.state.stickyNav} /> */}
        <div id="main">
          <section
            id="first"
            className="main special"
            style={{ textAlign: 'left' }}
          >
            <h2 style={{ textAlign: 'left' }}>{assignment.title}</h2>
            <div>
              <div
                dangerouslySetInnerHTML={{ __html: assignment.description }}
              />

              {assignment.expiryDate != null ? (
                <div>
                  <br />ExpiryDate: {assignment.expiryDate}
                </div>
              ) : (
                ''
              )}
              {assignment.author.name != null ? (
                <div>
                  <br />Submitted By: {assignment.author.name}
                </div>
              ) : (
                ''
              )}
              {assignment.author.email != null ? (
                <div>
                  <br />Email: {assignment.author.email}
                </div>
              ) : (
                ''
              )}
              {assignment.terms.name != null ? (
                <div>
                  <br />Category Name: {assignment.terms.name}
                </div>
              ) : (
                ''
              )}
              {assignment.actualUrl != null ? (
                <div>
                  <br />ActualUrl: {assignment.actualUrl}
                </div>
              ) : (
                ''
              )}
            </div>
            <Link to="/" style={{ textDecoration: `none`, cursor: 'pointer' }}>
              Go Back
            </Link>
          </section>
        </div>
      </div>
    )
  }
}

AssignmentTemplate.propTypes = propTypes

export default AssignmentTemplate

export const AssignmentsPageQuery = graphql`
  query getAllassignmentBYId($id: String!) {
    assignments: assignment(id: { eq: $id }) {
      id
      postId
      title
      description
      author {
        name
        email
      }
      expiryDate
      actualUrl
      terms {
        categoryId
        name
      }
    }
  }
`



